<template>
  <el-container style="margin-top: -10px">
    <el-main>
      <el-row :gutter="20">
        <el-col :span="10">
          <el-card shadow="always">
            <div slot="header">
              <span>数据权限</span>
            </div>
            <privilege-tree
                :show-action-button="true"
                :ref="refs.tree"
                @clickNode="clickNode"
                @add="add"
                @edit="edit"
                @remove="remove">
            </privilege-tree>
          </el-card>
        </el-col>
        <el-col :span="14">
          <privilege-form
            :ref="refs.form"
            :content="form.content"
            :action="form.action"
            :disabled="form.disabled"
            :show.sync="form.show"
            @refreshTree="refreshTreeNode">
          </privilege-form>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
  import PrivilegeTree from './PrivilegeTree';
  import PrivilegeForm from './PrivilegeForm';
  export default {
    name: 'privilegeSection',
    components: {
      'privilege-tree': PrivilegeTree,
      'privilege-form': PrivilegeForm,
    },
    data() {
      return {
        refs: {
          tree: 'tree',
          form: 'form',
        },
        form: {
          content: {},
          show: false,
          disabled: true,
          action: 'detail'
        }
      };
    },
    methods: {
      clickNode(data, node, self) {
        this.form.content = {
          parentId: data.parentId,
          id: data.id,
          name: data.name,
          code: data.code,
          path: data.path,
        };
        this.form.show = true;
        this.form.disabled = true;
        this.form.action = 'detail';
      },
      add(event, data, node) {
        event.stopPropagation();
        this.form.show = true;
        this.form.disabled = false;
        this.form.action = 'add';
        this.form.childCount = 0;
        this.form.content = {
          parentId: node ? data.id : '-1',
          path: node ? data.path : '',
        };
      },
      edit(event, data, node) {
        event.stopPropagation();
        this.form.show = true;
        this.form.disabled = false;
        this.form.action = 'edit';
        this.form.content = {
          parentId: data.parentId,
          id: data.id,
          name: data.name,
          code: data.code,
          path: data.path,
        };
      },
      async remove(event, data, node) {
        event.stopPropagation();
        if (!node.isLeaf) {
          this.$message({
            showClose: true,
            message: '该节点下存在子节点，不允许删除！',
            type: 'error'
          });
          return;
        }
        this.$confirm(`此操作将删除数据权限【${data.name}】，是否继续？`, '删除', {
          type: 'warning'
        }).then( async () => {
          let result = await this.$api.privileges.deletePrivilege(data.id);
          this.$message({
            showClose: true,
            message: result.data.meta.message,
            type: result.data.meta.success ? 'success' : 'error'
          });
          this.form.show = false;
          this.refreshTreeNode( data, true);
        });
      },
      async refreshTreeNode(content, isAdd) {
        this.form.disabled = true;
        let parentId = content.parentId;
        let nodes = [];
        let response = await this.$api.privileges.gerPrivilegeList(parentId, '');
        response.data.data && response.data.data.forEach( (node) => {
          nodes.push({
            id: node.id,
            code: node.code,
            name: node.name,
            parentId: node.parentId,
            path: node.path,
            leaf: node.childCount === 0
          });
        });
        if (parentId == -1) {
          this.$refs[this.refs.tree].initTreeNodes();
          return 1;
        }
        let nodeId = parentId;
        let node = this.$refs[this.refs.tree].getElementTreeRef().getNode(nodeId);
        node.expand();
        this.$refs[this.refs.tree].getElementTreeRef().updateKeyChildren(nodeId, nodes);
        return 1;
      }
    }
  };
</script>

