<template>
  <el-card shadow="always" v-show="show === true">
    <div slot="header">
      <span>数据权限信息</span>
    </div>
    <el-form :ref="ref" :model="content" :rules="rules" label-width="120px" :disabled="disabled">
      <el-form-item label="数据权限编码" prop="code">
        <el-input v-model="content.code" :disabled="disabled"></el-input>
      </el-form-item>
      <el-form-item label="数据权限名称" prop="name">
        <el-input v-model="content.name" :disabled="disabled"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">提交</el-button>
        <el-button @click="cancelAction">取消</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
  export default {
    name: "PrivilegeForm",
    props: ['content', 'show', 'action', 'disabled'],
    data() {
      return {
        ref: 'form',
        selects: [],
        rules: {
          code: [{required: true, message: '请输入数据权限编码', trigger: 'blur'}],
          name: [{required: true, message: '请输入数据权限名称', trigger: 'blur'}]
        }
      }
    },
    methods: {
      cancelAction() {
        this.$emit('update:show', false);
      },
      onSubmit() {
        this.$refs[this.ref].validate( async (valid) => {
          if(valid){
            let isAdd = this.action == 'add' ? true : false;
            let response = await (
              isAdd
                ? this.$api.privileges.addPrivilege(this.content)
                : this.$api.privileges.editPrivilege(this.content)
            )
            if(response.data.meta && response.data.meta.success){
              this.$message({
                showClose: true,
                message: response.data.meta.message,
                type: 'success'
              });
              this.$emit('refreshTree',this.content, isAdd);
            }else {
              this.$message({
                dangerouslyUseHTMLString: true,
                showClose: true,
                message: response.data.meta.message,
                type: 'error'
              });
            }
          }
        })
      }
    },
    watch: {
      'action'(newV, oldV) {
        this.$refs.form.resetFields();
      }
    },
  }
</script>

